// App-specific variables
// =========================
// shared
$reports_chartColors: $brand_chartColors !default;

// module(s)
$reports_modDash_bgColor: $brand_color_white !default;

$reports_modDash_favRep_width: 0.75 !default;
$reports_modDash_favRep_height: 280px !default;
// calculating heights above mobile:            ((max screen width        - sidebar width (if appropo)         - 2 * outer padding)                    * percent width of favs col     - (space between two))                  / divided by 2
// $reports_modDash_favRep_height_tablet:       (($breakpoint_tablet_max                                       - ($g6ModulePaddingX_tablet * 2))       * $reports_modDash_favRep_width - ($g6ModulePaddingX_tablet / 2))       / 2 !default;
// $reports_modDash_favRep_height_desktop:      (($breakpoint_desktop_max - $global_sidebar_width_desktop      - ($g6ModulePaddingX_desktop * 2))      * $reports_modDash_favRep_width - ($g6ModulePaddingX_desktop / 2))      / 2 !default;
// $reports_modDash_favRep_height_desktopLarge: (($breakpoint_desktop_max - $global_sidebar_width_desktopLarge - ($g6ModulePaddingX_desktopLarge * 2)) * $reports_modDash_favRep_width - ($g6ModulePaddingX_desktopLarge / 2)) / 2 !default;
$reports_modDash_favRep_height_tablet:       300px !default;
$reports_modDash_favRep_height_desktop:      320px !default;
$reports_modDash_favRep_height_desktopLarge: 360px !default;

$reports_modDash_favRep_bgColor: $brand_color_white !default;
$reports_modDash_favRep_borderColor: $brand_color_border !default;
$reports_modDash_favRep_borderWidth: 0 !default;
$reports_modDash_favRep_iconsColor: $brand_color_black !default;
$reports_modDash_favRep_iconsWidth: $headline5fontSize_mobile !default;
$reports_modDash_favRep_parametersColor: $brand_color_mid !default;

$reports_modDash_allRep_minWidth: 256px !default;
$reports_modDash_allRep_minWidth_desktop: 320px !default;
$reports_modDash_allRep_bgColor: $brand_color_stripe !default;

$reports_manageFavoritesPopoverBorderColor: $g5ModPgBorderColor !default;
$reports_changeFiltersPopoverBorderColor: $g5ModPgBorderColor !default;

// page
$reports_chart_border_width: 2px !default;
$reports_chart_border_color: $brand_color_mid !default;
$reports_chartThumb_border_width: 2px !default;
$reports_chartThumb_border_color: $brand_color_dark !default;
$reports_chartThumb_border_colorSelected: $brand_color_primary !default;

// App-specific code
// ==================================================

/* ----- GLOBAL APP STYLES ----- */
// Note that every module and page and related instance of this app should be namespaced with the appName
.reports {
    .ui-tooltip.qtip {
        // z-index: 9999 !important;
    }
} /* /.reports */


/* ----- MODULE STYLES ----- */
// .reports:first-of-type {
//     width: 70%;
// }
.launchModule.reports {
    $favWidth: $reports_modDash_favRep_width * 100%;
    $allWidth: 100% - $favWidth;

    &.reportsDashboard {
        background: $reports_modDash_bgColor;
        border-top: $pages_pageContentBorderWidth solid $pages_pageContentBorderColor;

        .nav-tabs {
        }

        .tab-pane {
        }
        .tab-content {
            overflow: visible;
            @include clearfix();

            .reportHeader {
                display: none;
                margin: 0 0 $baseLineHeight 0;
                font-size: $baseFontSize;
                line-height: $lineHeightSmall;
                font-weight: $fontWeightSemiBold;
                letter-spacing: 1px; // TODO: GLOBAL NEEDED
                text-transform: uppercase; // TODO: GLOBAL NEEDED
                color: $brand_color_primary;
            }
        }

        .dashboard-favorites {
        }

        .favoritesContainer {
            position: relative;
            margin: 0;
            padding: 0;
            min-height: $g5ModuleUnit;
            @include clearfix();
        }
        .favorite {
            @if $reports_chartColors {
                &:before {
                    content: "#{$reports_chartColors}";
                    display: none;
                }
            }

            position: relative;
            margin: 0;
            // padding: ($g6ModulePaddingY / 2) $g6ModulePaddingX;
            width: 100%;
            height: $reports_modDash_favRep_height;
            background: $reports_modDash_favRep_bgColor;
            border: $reports_modDash_favRep_borderWidth solid $reports_modDash_favRep_borderColor;
            @include box-sizing(border-box);
            overflow: hidden; // temp, hopefully

            & ~ .favorite {
                margin-top: nth($card_margin, 1);
            }

            &:first-child.ui-sortable-helper + .favorite {
                margin-top: 0;
            }

            &.placeholder {
                background: transparentize($reports_modDash_favRep_borderColor, 0.5);
                border-radius: $card_borderRadius;
            }

            .reorder,
            .visitAppBtn {
                position: absolute;
                z-index: 99;
                top: $reports_modDash_favRep_iconsWidth / 3;
                left: $reports_modDash_favRep_iconsWidth / 3;
                width: $reports_modDash_favRep_iconsWidth;
                height: $reports_modDash_favRep_iconsWidth;
                font-size: $reports_modDash_favRep_iconsWidth;
                line-height: $reports_modDash_favRep_iconsWidth;
                // font-weight: ;
                // color: ;
                cursor: move;
                @include ignoreGlobalLinkStyles(
                    $color: $reports_modDash_favRep_iconsColor false,
                    $fontWeight: $fontWeightBold false
                );
            }
            .visitAppBtn {
                left: auto;
                right: $reports_modDash_favRep_iconsWidth / 3;
                cursor: pointer;

                i {
                    width: 1em;
                    font-weight: inherit;
                }
            }

            .displayName {
                margin: 0;
                font-size: $fontSizeMid;
                line-height: $lineHeightMid;
                font-weight: $fontWeightBold;

                a {
                    display: inline-block;
                    max-width: 100%;
                    white-space: nowrap;
                    overflow: hidden;
                    text-overflow: ellipsis;
                }
            }
            .parameters {
                font-size: $fontSizeSmall;
                line-height: $lineHeightSmall;
                color: $reports_modDash_favRep_parametersColor;
            }
            .chartContainer {
                position: absolute;
                top: nth($card_padding, 1) + $lineHeightMid + ($lineHeightSmall * 2);
                right: 0;
                bottom: 0;
                left: 0;
            }

            .emptyTitle {
                position: absolute;
                top: 0;
                right: 0;
                bottom: 0;
                left: 0;
                margin: 0;
                line-height: $reports_modDash_favRep_height;
                vertical-align: middle;
                text-align: center;

                a {
                    display: inline-block;
                    padding: 10px;
                    line-height: $headlineLineHeight;
                    color: $reports_modDash_favRep_parametersColor;
                    vertical-align: middle;
                }
                .subtitle {
                    display: block;
                    font-size: 0.75em;
                }
            }
        }
        .no-sorting {
            .reorder {
                display: none;
            }
        }

        .dashboard-all {
            .allContainer {
                position: relative;
                min-height: $g5ModuleUnit;
            }

            .span11 {
                float: none;
                margin: 0;
                width: 100%;
                padding-bottom: $baseLineHeight;
            }
            h5 {
                font-size: $headline5fontSize_mobile;
                margin-top: 0;
                margin-bottom: 0;
            }
            .switcher {
                cursor: pointer;

                i {
                    float: right;
                    font-weight: $fontWeightBold;
                }
            }
            .reportslist {
                list-style: none;
                margin-bottom: 0;
            }
            .reportReport {
                margin-top: $baseLineHeight / 2;

                & + .reportReport {
                    margin-top: $baseLineHeight;
                }

                a {
                    font-size: $fontSizeMid;
                    line-height: $lineHeightMid;
                    //font-weight: $fontWeightBold;
                }

                .desc {
                    font-size: $fontSizeSmall;
                    line-height: $lineHeightSmall;
                }
            }
        }

        @mixin reportsFavorites($paddingY, $paddingX, $allMinWidth, $favHeight, $favTopMargin) {
            .dashboard-favorites {
                padding-right: ($paddingX / 2);
                width: calc(#{$favWidth} - #{$paddingX / 2});
                max-width: calc(100% - #{$allMinWidth - ($paddingX / 2)});
            }
            .favorite {
                // padding: ($paddingY / 2) ($paddingX / 2);
                height: $favHeight;

                & ~ .favorite {
                    margin-top: $favTopMargin;
                }
                &:first-child.ui-sortable-helper + .favorite {
                    margin-top: 0;
                }
            }

            .dashboard-all {
                margin: (-1 * $paddingY) (-1 * $paddingX) (-1 * $paddingY) 0;
                padding: $paddingY $paddingX $paddingY ($paddingX / 2);
                width: calc(#{$allWidth} - #{$paddingX / 2});
                min-width: $allMinWidth - ($paddingX * 3 / 2);
            }
        }
        @mixin reportsTwoColumns($rightMargin) {
            .favoritesContainer {
                margin-right: -1 * $rightMargin;
            }
            .favorite {
                width: calc(100% - #{$rightMargin});

                & ~ .favorite {
                    float: left;
                    margin-right: $rightMargin;
                    width: calc(50% - #{$rightMargin});
                }
                &:first-child.ui-sortable-helper + .favorite {
                    width: calc(100% - #{$rightMargin});
                }
            }
        }

        // EXTRA breakpoint for shrinking the sortable items on touch devices
        // @media (max-width: $breakpoint_tablet_max) {
        .touch & {
            .favoritesContainer.ui-sorting {
                .favorite {
                    // same as .chartContainer top above
                    height: nth($card_padding, 1) + $lineHeightMid + ($lineHeightSmall * 2);

                    .chartContainer {
                        display: none;
                    }

                    .emptyTitle {
                        line-height: nth($card_padding, 1) + $lineHeightMid + ($lineHeightSmall * 2);

                        a {
                            padding: 0;
                        }
                    }

                    &.ui-sortable-helper {
                        .chartContainer {
                            display: block;
                        }
                    }
                }
            }
        }
        // }

        // EXTRA breakpoint for two columns on mobile
        @media (max-width: $breakpoint_mobile_max) and (min-width: ($reports_modDash_favRep_height * 2 + $g6ModulePaddingX * 3)) {
            @include reportsTwoColumns(nth($card_margin, 2));
        }

        @media (min-width: $breakpoint_tablet_min) {
            border-top-width: $pages_pageContentBorderWidth_tablet;

            .nav-tabs {
                display: none;
            }
            .tab-pane {
                display: block;
            }
            .tab-content {
                .reportHeader {
                    display: block;
                }
            }

            .dashboard-favorites {
                float: left;
            }
            .favorite {
                .reorder,
                .visitAppBtn {
                    top: $reports_modDash_favRep_iconsWidth / 2;
                    left: $reports_modDash_favRep_iconsWidth / 2;
                }
                .visitAppBtn {
                    left: auto;
                    right: $reports_modDash_favRep_iconsWidth / 2;
                }
            }

            .dashboard-all {
                float: right;
                background: $reports_modDash_allRep_bgColor;
            }

            @include reportsFavorites(
                $g6ModulePaddingY_tablet,
                $g6ModulePaddingX_tablet,
                $reports_modDash_allRep_minWidth,
                $reports_modDash_favRep_height_tablet,
                nth($card_margin_tablet, 1)
            );
        } // /@media

        // EXTRA breakpoint for two columns on tablet
        @media (max-width: $breakpoint_tablet_max) and (min-width: ($reports_modDash_favRep_height_tablet * 2 + $g6ModulePaddingX_tablet * 2 + $reports_modDash_allRep_minWidth)) {
            @include reportsTwoColumns(nth($card_margin_tablet, 2));
        }

        @media (min-width: $breakpoint_desktop_min) {
            border-top-width: $pages_pageContentBorderWidth_desktop;

            @include reportsFavorites(
                $g6ModulePaddingY_desktop,
                $g6ModulePaddingX_desktop,
                $reports_modDash_allRep_minWidth_desktop,
                $reports_modDash_favRep_height_desktop,
                nth($card_margin_desktop, 1)
            );
        } // /@media

        // EXTRA breakpoint for two columns on desktop
        @media (max-width: $breakpoint_desktop_max) and (min-width: ($reports_modDash_favRep_height_desktop * 2 + $g6ModulePaddingX_desktop * 2 + $reports_modDash_allRep_minWidth_desktop + $global_sidebar_width_desktop)) {
            @include reportsTwoColumns(nth($card_margin_desktop, 2));
        }

        @media (min-width: $breakpoint_desktopLarge_min) {
            border-top-width: $pages_pageContentBorderWidth_desktopLarge;

            @include reportsFavorites(
                $g6ModulePaddingY_desktopLarge,
                $g6ModulePaddingX_desktopLarge,
                $reports_modDash_allRep_minWidth_desktop,
                $reports_modDash_favRep_height_desktopLarge,
                nth($card_margin_desktopLarge, 1)
            );
        } // /@media

        // EXTRA breakpoint for two columns on desktopLarge
        @media (max-width: $breakpoint_desktopLarge_max) and (min-width: ($reports_modDash_favRep_height_desktopLarge * 2 + $g6ModulePaddingX_desktopLarge * 2 + $reports_modDash_allRep_minWidth_desktop + $global_sidebar_width_desktopLarge)) {
            @include reportsTwoColumns(nth($card_margin_desktopLarge, 2));
        }
    }


