    #reportsPageAllView {

        .reportCategory {
            ul {
                margin-left: 0;
                li {
                    list-style-type: none;
                    margin-bottom: $baseLineHeight;
                }
            }
        }
        @media (max-width: 979px) {
            .reportCategory {
                .span1 {
                    line-height: $baseLineHeight * 2 + ($baseLineHeight / 2) * 2; // h1, h2, h3 line-height + h* top/bottom-margin * 2
                }
            }
        }
        @media (max-width: 767px) {
            .reportCategory {
                .span1 {
                    display: inline;
                    float: left;
                    margin: 0 12px 0 0;
                    width: 36px;
                }
                .span11 h4 {
                    margin-bottom: $baseLineHeight;
                }
            }
        }


    } /* /#reportsPageAllView */

    #reportsPageDetailView {
        // position: relative;
        @if $reports_chartColors {
            &:before {
                content: "#{$reports_chartColors}";
                display: none;
            }
        }

        #reportDesc {
            margin-top: -1 * ($baseLineHeight / 2);
        }
        #timeStamp {
        }

        #chartRowContainer {
        }


        #chartRow {
            $chart_max_width_mobile: $breakpoint_mobileMini_max - ($g6PagePaddingX * 2);
            $chart_gap_mobile: $g6PagePaddingY / 2;
            $chartThumb_gap_mobile: $g6PagePaddingX / 2;
            $chartThumb_max_width_mobile: ($chart_max_width_mobile - ($chartThumb_gap_mobile * 5)) / 6;
            $chartCaption_min_width_mobile: false;

            $chart_min_width_tablet: 384px;
            $chart_max_width_tablet: $chart_max_width_mobile;
            $chart_gap_tablet: $g6PagePaddingX_tablet / 2;
            $chartThumb_gap_tablet: $g6PagePaddingX_tablet / 3;
            $chartThumb_max_width_tablet: ($chart_max_width_tablet - ($chartThumb_gap_tablet * 5)) / 6;
            $chartCaption_min_width_tablet: 256px;

            $reports_breakpoint_tablet_supp: $g6PagePaddingX_tablet + $chartThumb_max_width_tablet + $chart_gap_tablet + $chart_min_width_tablet + $chart_gap_tablet + $chartCaption_min_width_tablet + $g6PagePaddingX_tablet;
            $reports_breakpoint_desktop_supp1: $breakpoint_tablet_max + ($global_sidebar_width_desktop / 2);
            $reports_breakpoint_desktop_supp2: $breakpoint_tablet_max + $global_sidebar_width_desktop;

            $chart_max_width_desktop: $breakpoint_tablet_max - ($g6PagePaddingX_desktop * 2) - $global_sidebar_width_desktop;
            $chart_gap_desktop: $g6PagePaddingX_desktop / 2;
            $chartThumb_gap_desktop: $g6PagePaddingX_desktop / 4;
            $chartThumb_max_width_desktop: ($chart_max_width_desktop - ($chartThumb_gap_desktop * 5)) / 6;
            $chartCaption_min_width_desktop: 256px;

            .span7 {
                margin: 0 0 $baseLineHeight 0;
                width: auto;
                @include clearfix();
                // background: orange;

                @media (min-width: $breakpoint_tablet_min) {
                    margin-right: $chart_gap_tablet;
                    width: calc(100% - #{$chart_gap_tablet + $chartCaption_min_width_tablet});
                    max-width: $chartThumb_max_width_tablet + $chart_gap_tablet + $chart_max_width_tablet;
                }

                // for a short period at desktop size, we push the caption down below again
                @media (min-width: $breakpoint_desktop_min) and (max-width: $reports_breakpoint_desktop_supp1) {
                    float: none;
                    margin-right: 0;
                    width: auto;
                    max-width: none;
                }
                @media (min-width: $reports_breakpoint_desktop_supp1 + 1) and (max-width: $reports_breakpoint_desktop_supp2) {
                    max-width: none;
                }

                // now flip back to the expected orientation
                @media (min-width: $reports_breakpoint_desktop_supp2 + 1) {
                    margin-right: $chart_gap_desktop;
                    width: calc(100% - #{$chart_gap_desktop + $chartCaption_min_width_desktop});
                    max-width: $chartThumb_max_width_desktop + $chart_gap_desktop + $chart_max_width_desktop;
                }
            }

            #chartInFocus {
                position: relative;
                margin: 0 auto;
                width: 100%;
                height: $chart_max_width_mobile;
                max-width: $chart_max_width_mobile;
                background-color: $white;
                border: $reports_chart_border_width solid $reports_chart_border_color;
                overflow: hidden;
                // background: blue;

                .reportsChart-moveable-item {
                    position: absolute;
                    top: 0;
                    left: -100%;
                    width: 100% !important;
                    height: 100% !important;
                    display: block !important;
                    @include transition-property(left);
                    @include transition-duration($animation_duration);

                    &.active {
                        left: 0;

                        & ~ .reportsChart-moveable-item {
                            left: 100%;
                        }
                    }

                    .individualChartBkgd {
                        display: none;
                        width: 100%;
                        height: 100%;
                        // @include opacity(.25);
                    }
                }

                @media (min-width: $breakpoint_mobileMini_min) {
                    width: calc(100% - #{$chartThumb_max_width_mobile + $chart_gap_mobile + ($reports_chart_border_width * 2)});
                    max-width: none;
                }

                // for a short period at tablet size, we put the thumbs above the chart
                @media (min-width: $breakpoint_tablet_min) and (max-width: $reports_breakpoint_tablet_supp) {
                    width: calc(100% - #{($reports_chart_border_width * 2)});
                    max-width: $chart_max_width_tablet - ($reports_chart_border_width * 2);
                    height: $chart_max_width_tablet - $chartThumb_max_width_tablet - $chart_gap_tablet - ($reports_chart_border_width * 2);
                }

                // now flip back to the expected orientation
                @media (min-width: $reports_breakpoint_tablet_supp + 1) {
                    width: calc(100% - #{$chartThumb_max_width_tablet + $chart_gap_tablet + ($reports_chart_border_width * 2)});
                }

                // for a short period at desktop size, we push the caption down below again
                @media (min-width: $breakpoint_desktop_min) and (max-width: $reports_breakpoint_desktop_supp2) {
                    // width: calc(100% - #{$chartThumb_max_width_tablet + $chart_gap_tablet + ($reports_chart_border_width * 2)});
                    max-width: none;
                    height: $chart_max_width_tablet - ($reports_chart_border_width * 2);
                }

                // now flip back to the expected orientation
                @media (min-width: $reports_breakpoint_desktop_supp2 + 1) {
                    width: calc(100% - #{$chartThumb_max_width_desktop + $chart_gap_desktop + ($reports_chart_border_width * 2)});
                    max-width: $chart_max_width_desktop - ($reports_chart_border_width * 2);
                    height: $chart_max_width_desktop - ($reports_chart_border_width * 2);
                }
            }

            .chartThumbs {
                list-style: none;
                margin: 0 auto $chart_gap_mobile auto;
                @include clearfix();
                // background: green;

                li {
                    display: inline;
                    float: left;
                    position: relative;
                    width: calc((100% - #{$chartThumb_gap_mobile * 5}) / 6);
                    max-width: $chartThumb_max_width_mobile;
                    max-height: $chartThumb_max_width_mobile;
                    background: lighten($grayLighter, 3%);
                    // @include opacity(.25);

                    & ~ li {
                        margin-left: $chartThumb_gap_mobile;
                    }

                    a {
                        display: block;
                        cursor: pointer;
                        @include ignoreGlobalLinkStyles();
                    }

                    img {
                        border: $reports_chartThumb_border_width solid $reports_chartThumb_border_color;
                        @include opacity(0.33);
                        width: calc(100% - #{($reports_chartThumb_border_width * 2)});
                        height: calc(100% - #{($reports_chartThumb_border_width * 2)});
                        cursor: pointer;
                    }

                    i {
                        display: none;
                        position: absolute;
                        right: 50%;
                        bottom: -1 * $chart_gap_mobile / 2;
                        margin: 0 (-1 * $chart_gap_mobile / 2) (-1 * $chart_gap_mobile / 2) 0;
                        width: $chart_gap_mobile;
                        height: $chart_gap_mobile;
                        font-size: $chart_gap_mobile;
                        line-height: $chart_gap_mobile;
                        font-weight: $fontWeightBold;
                        text-align: center;
                        color: $reports_chartThumb_border_colorSelected;
                        // background: purple;
                    }

                    &.activeSlide {
                        // @include opacity(1);

                        a {
                            cursor: default;
                        }

                        img {
                            border-color: $reports_chartThumb_border_colorSelected;
                            @include opacity(1);
                            cursor: default;
                        }

                        .icon-arrow-1-down {
                            display: block;
                        }
                    }

                    // a {
                    //     .individualChartThumb {
                    //         width: 100%;
                    //         height: 100%;
                    //     }
                    // }
                }

                @media (min-width: $breakpoint_mobileMini_min) {
                    display: inline;
                    float: left;
                    margin: 0 $chart_gap_mobile 0 0;
                    width: $chartThumb_max_width_mobile;

                    li {
                        width: 100%;
                        max-width: 100%;
                        max-height: none;

                        & ~ li {
                            margin-top: $chartThumb_gap_mobile;
                            margin-left: 0;
                        }

                        i {
                            right: -1 * $chart_gap_mobile / 2;
                            bottom: 50%;
                        }

                        &.activeSlide {
                            .icon-arrow-1-down {
                                display: none;
                            }
                            .icon-arrow-1-right {
                                display: block;
                            }
                        }
                    }
                }

                // for a short period at tablet size, we put the thumbs above the chart
                @media (min-width: $breakpoint_tablet_min) and (max-width: $reports_breakpoint_tablet_supp) {
                    display: block;
                    float: none;
                    margin: 0 0 $chart_gap_tablet 0;
                    width: auto;

                    li {
                        width: calc((100% - #{$chartThumb_gap_tablet * 5}) / 6);
                        max-width: $chartThumb_max_width_tablet;
                        max-height: $chartThumb_max_width_tablet;

                        & ~ li {
                            margin-left: $chartThumb_gap_tablet;
                            margin-top: 0;
                        }

                        i {
                            bottom: -1 * $chart_gap_tablet / 2;
                            right: 50%;
                        }

                        &.activeSlide {
                            .icon-arrow-1-down {
                                display: block;
                            }
                            .icon-arrow-1-right {
                                display: none;
                            }
                        }
                    }
                }

                // now flip back to the expected orientation
                @media (min-width: $reports_breakpoint_tablet_supp + 1) {
                    margin-right: $chart_gap_tablet;
                    width: $chartThumb_max_width_tablet;

                    li {
                        i {
                            right: -1 * $chart_gap_tablet / 2;
                            margin: 0 (-1 * $chart_gap_tablet / 2) (-1 * $chart_gap_tablet / 2) 0;
                            width: $chart_gap_tablet;
                            height: $chart_gap_tablet;
                            font-size: $chart_gap_tablet;
                            line-height: $chart_gap_tablet;
                        }

                        & ~ li {
                            margin-top: $chartThumb_gap_tablet;
                        }
                    }
                }

                @media (min-width: $breakpoint_desktop_min) {
                }

                // for a short period at desktop size, we push the caption down below again
                @media (min-width: $breakpoint_desktop_min) and (max-width: $reports_breakpoint_desktop_supp2) {
                    // margin-right: $chart_gap_desktop;
                    // width: $chartThumb_max_width_tablet;
                }

                // now flip back to the expected orientation
                @media (min-width: $reports_breakpoint_desktop_supp2 + 1) {
                    margin-right: $chart_gap_desktop;
                    width: $chartThumb_max_width_desktop;

                    li {
                        i {
                            right: -1 * $chart_gap_desktop / 2;
                            margin: 0 (-1 * $chart_gap_desktop / 2) (-1 * $chart_gap_desktop / 2) 0;
                            width: $chart_gap_desktop;
                            height: $chart_gap_desktop;
                            font-size: $chart_gap_desktop;
                            line-height: $chart_gap_desktop;
                        }

                        & ~ li {
                            margin-top: $chartThumb_gap_desktop;
                        }
                    }
                }
            }

            .span5 {
                float: none;
                margin: 0;
                width: auto;
                // background: yellow;

                #exportChartPdf {
                    // temporarily hiding the PDF icon until FusionCharts gets us a version of their library that isn't broken
                    //Enable pdf link for temporary testing purpose in pentagqa
                    //display: none;

                    float: right;
                }
            }

        }

        #chartName {
            // color: $gray;
        }
        .reportsChangeFiltersPopoverTrigger {
            // margin-left: 4px;
        }
        #currentParametersSummary {
            // margin-top: 13px;
            // margin-bottom: 36px;

            @media (min-width: 768px) and (max-width: 979px) {
                strong {
                    display: block;
                }
            }
        }

        .paginationControls {
            position: relative;
            // margin-top: -23px;
        }

        #tabularResultsContainer {
            position: relative;

            .columnDefinitions-popover {
                display: inline-block;
                cursor: pointer;
                @include ignoreGlobalLinkStyles();
            }

            #resultsTableWrapper {
                width: 100%;
                overflow: visible;
                font-size: $baseFontSize * .857; // converts 14px to 12px elegantly
            }
            .pagination ~ #resultsTableWrapper,
            #resultsTableWrapper ~ .pagination {
                margin-top: -1 * $baseLineHeight / 2;
            }

            thead th {
                white-space : nowrap;
            }

            table {
                .info.summaryRow {
                    font-weight: bold;
                }
            }
        }

        .columnDefinitionsPopover {
            max-width: ($gridColumns / 2) * ($gridColumnWidth + $gridGutterWidth) - $gridGutterWidth;
            width: auto  !important;
            border-color: $grayLight;
            @include box-shadow(2px 4px 24px rgba(0,0,0,.5));

            .row {
                margin: 0;
            }

            #columnDefinitions {
                margin: 0;
            }
        }

    } /* /#reportsPageDetailView */

} /* /.page.reports */

