    left: 50% !important;
    font-size: $baseFontSize;
    line-height: $baseLineHeight;

    z-index: 20000 !important; // greater than default qtip (and pretty much everything else) // lifted from _modals.scss
    position: fixed !important; // lifted from bootstrap/_modals.scss
    top: 10% !important; // lifted from bootstrap/_modals.scss
    width: 560px !important; // lifted from bootstrap/_modals.scss
    border: $modal_borderWidth solid $modal_borderColor; // lifted from _modals.scss
    @include box-shadow(0 3px 7px rgba(0,0,0,0.3)); // lifted from bootstrap/_modals.scss

    @media (max-width: 767px) {
        top: 20px !important; // lifted from bootstrap/_modals.scss
        left: 20px !important; // lifted from bootstrap/_modals.scss
        right: 20px !important; // lifted from bootstrap/_modals.scss
        width: auto !important; // lifted from bootstrap/_modals.scss
    }

    .ui-tooltip-content {
        overflow: visible;
        padding: 0;
    }

    #dragToChange {
        font-style: italic;
    }

    .myFavoriteReports {
        background: $brand_color_light;

        .dragItem {
            margin: 0;
            background: $white;
            cursor: move;

            & ~ .dragItem {
                margin-top: 1px;
            }

            &.ui-sortable-helper {
                @include opacity(.8);
            }
        }
    }

    $dragHeight: 36px;

    .dragItem {
        padding: 4px 8px;
        min-height: $dragHeight;
        line-height: $dragHeight;
        vertical-align: middle;

        .move,
        .remove {
            display: inline;
            float: left;
            margin-top: 0;
            margin-left: 0;
            height: $dragHeight;
            width: 30px;
            font-size: 21px;
            line-height: $dragHeight;
            vertical-align: middle;
        }
        .remove {
            float: right;
            // margin-left: 0;
            //color: $grayLight;
            // margin-top: 5px;
            // margin-left: 2px;
            cursor: pointer;
            text-align: center;
        }
    }

    .onlyOneFavorite {
        .move,
        .remove {
            display: none;
        }
    }

    .reportFavoriteIcon {
        display: inline;
        float: left;
        margin: 0 8px 0 0;
        width: $dragHeight - 2px;
        height: $dragHeight - 2px;
        border: 1px solid $brand_color_border;

        img {
            display: block;
        }
    }
    .reportFavoriteText {
        p {
            display: block;
            margin: 0;
            line-height: normal;
            vertical-align: middle;
        }
    }

    .sortable {
        list-style-type: none;
        margin: 0;
        padding: 0;
    }
}

