// App-specific variables
// =========================
$purlCelebrate_moduleBgColor: #120E25 !default;
$purlCelebrate_moduleBgImg: "celebration/upcomingCelebrations.jpg" !default;
$purlCelebrate_moduleBgImgPath: $appImagesPath !default;
$purlCelebrate_moduleBgImgSize: cover !default;
$purlCelebrate_moduleBgImgPosition: 50% 50% !default;

$purlCelebrate_pageTopperColor: $brand_color_white !default;
$purlCelebrate_pageTopperBgColor: $purlCelebrate_moduleBgColor !default;
$purlCelebrate_pageTopperBgImg: $purlCelebrate_moduleBgImg !default;
$purlCelebrate_pageTopperBgImgPath: $purlCelebrate_moduleBgImgPath !default;
$purlCelebrate_pageTopperBgImgSize: $purlCelebrate_moduleBgImgSize !default;
$purlCelebrate_pageTopperBgImgPosition: $purlCelebrate_moduleBgImgPosition !default;
$purlCelebrate_pageTopperBgImgOpacity: 0.5 !default;

$purlCelebrate_titleColor: $brand_color_white !default;
$purlCelebrate_titleSpanColor: $purlCelebrate_titleColor !default;
$purlCelebrate_titleNumberColor: $purlCelebrate_titleColor !default;

$purlCelebrate_fontFamily: $titleFont !default;
$purlCelebrate_moduleListInfoColor: $brand_color_white !default;
$purlCelebrate_modulePopoverColor: $brand_color_white !default;

$purlCelebrate_magnifyColor: $gray !default;

// App-specific code
// ==================================================

/* ----- MODULE STYLES ----- */
.launchModule.purlCelebrate {

    .module-liner {
        padding: 0;
        height: auto;
        // TODO: THESE BACKGROUND RULES NEED TO BE SKINNABLE
        background-color: $purlCelebrate_moduleBgColor;
        @include safeImgUrls($purlCelebrate_moduleBgImg, $purlCelebrate_moduleBgImgPath, $property: 'background-image');
        background-size: $purlCelebrate_moduleBgImgSize;
        background-position: $purlCelebrate_moduleBgImgPosition;
    }

    .purlCelebrateModuleTop {
        width: 89%;
        max-width: $breakpoint_desktop_min;
        margin: 0 auto;
        .purlCelebrateModuleTitle {
            color: $purlCelebrate_titleColor;
            display: inline-block;
            font-family: $purlCelebrate_fontFamily;
            font-size: $baseFontSize ;
            font-weight: $fontWeightBold;
            line-height: $baseLineHeight;
            //margin: 15px 0 10px 55px;
            vertical-align: top;
            float: left;
            text-transform: uppercase;
        }

        a.viewMore {
            float: right;
            display: inline-block;
            line-height: 1em;
            //font-weight: $fontWeightBold;
            margin-top: 15px;
            margin-right: 5px;

            @media (max-width: $breakpoint_mobileMini_max) {
                margin-top: 12px;
            }
            @include ignoreGlobalLinkStyles(
                $color: $brand_color_white false
            );
        }
    }

    .purlCelebrateSubTitle {
        color: $purlCelebrate_titleSpanColor;
        display: block;
        font: $fontWeightLight $baseFontSize * 2.86 $purlCelebrate_fontFamily;
        line-height: $baseLineHeight * 2;
        margin: 0 0 0 15px;
    }

    .purlCelebrateTotal {
        color: $purlCelebrate_titleNumberColor;
        display: inline-block;
        margin-right: 2px;
    }

    .purlCelebrateList .carousel {
        width: 90%;
        margin: 0 auto;
        margin-bottom: 30px;
        max-height: 100px;
        padding-left: 10px;
        overflow: hidden;
        @media (max-width: $breakpoint_mobileMini_max) {
            width: 75%;
            padding-left: 0;
        }

        .slick-list {
            max-width: $breakpoint_desktop_min;
            margin: 0 auto;
            overflow: hidden;
            margin-right: -1px;
            @media (min-width: $breakpoint_desktopLarge_min) {
                margin: 0 auto;
            }
            @media (max-width: $breakpoint_tablet_max) {
                max-width: 600px;
            }
        }

        .slick-arrow {
            line-height: 195px;
            position: absolute;
            top: 0;
            font-size: 36px;

            @media (max-width: $breakpoint_mobile_max) {
                //display: none !important;
            }

            &.slick-prev {
                left: 0;
                color: $brand_color_white;
            }

            &.slick-next {
                right: 0;
                color: $brand_color_white;
                @media (max-width: $breakpoint_mobileMini_max) {
                    right: -3px;
                }

            }

            &:hover {
                cursor: pointer;
            }

            &.slick-disabled {
                visibility: hidden;
            }
        }

        .item {
            display: inline-block;
            list-style: none;
            padding: 0;
            border-radius: 8px;
            margin: 0;
            margin-right: 20px;
            @media (max-width: $breakpoint_mobileMini_max) {
                margin-right: 0;
            }
            background: $brand_color_white;
            width: 280px;
            max-height: 100px;

            img.avatar {
                float: left;
                height: 100px;
                width: 100px;
                border-radius: 0;
                border-top-left-radius: 8px;
                border-bottom-left-radius: 8px;
                background: none;
            }

            span.avatar {
                float: left;
                height: 100px;
                width: 100px;
                border-top-left-radius: 8px;
                border-bottom-left-radius: 8px;
                background: $brand_color_primary;
                color: $brand_color_white;
                font-size: $baseFontSize * 2.14;
                line-height: 100px;
                text-align: center;
                text-transform: uppercase;
            }

            .celebrationInfo {
                display: block;
                float: left;
                width: 50px;
                height: 50px;
                border: solid 3px $brand_color_white;
                background: $brand_color_black;
                border-radius: 50px;
                text-align: center;
                margin: 23px -5px 0 -27px;
                color: $purlCelebrate_titleSpanColor;
                position: relative;

                &.noAnniversary {
                    // background: none;
                    // border: none;
                    display: none;
                }

                .promotion {
                    position: absolute;
                    left: 0;
                    width: 100%;
                    height: 100%;
                    color: $brand_color_primary;
                    line-height: 50px;
                    font-size: 16px; //px value because it's dependent on the balloon's size
                    font-weight: $fontWeightBold;
                    z-index: 101;
                }

                .balloon {
                    @include safeImgUrls("celebration/upcomingCel-balloon.png", $appImagesPath, $property: 'background-image');
                    background-repeat: no-repeat;
                    background-size: 100%;
                    position: absolute;
                    left: 0;
                    width: 100%;
                    height: 100%;
                    z-index: 100;
                }
            }

            .personalInfo {
                display: block;
                float: left;
                line-height: $baseLineHeight;
                margin: 10px 15px;
                font-size: $baseFontSize * 1.14;
                font-weight: $fontWeightSemiBold;
                overflow: hidden;
                white-space: nowrap;
                text-overflow: ellipsis;
                width: 38%;

                @media (max-width: $breakpoint_mobileMini_max) {
                    font-size: $baseFontSize;
                }

                a.profile-popover {
                    @include ignoreGlobalLinkStyles(
                        $color: $brand_color_black
                    );
                }

                .celebrationTimeLeft {
                    display: block;
                    font-weight: $fontWeightBold;
                    font-size: $baseFontSize * 0.75;
                    color: $brand_color_darker;

                    i {
                        position: relative;
                        top: 1px;
                        padding-right: 2px;
                        font-weight: 800;
                    }

                    &.isToday {
                        color: $brand_color_alert;
                    }
                }

                a.shareLink {
                    font-size: $fontSizeSmall;
                    line-height: $lineHeightSmall;
                    display: block;
                    @media (max-width: $breakpoint_mobileMini_max) {
                        font-size: $fontSizeMini;
                        line-height: $lineHeightMini;
                    }

                }
            }
        }

        &.oneItem {
            text-align: center;

            .item {
                width: 280px;

                .personalInfo {
                    text-align: left;
                }
            }
        }

        &.twoItems {
            text-align: center;

            .item {
                width: 280px;

                .personalInfo {
                    text-align: left;
                }
            }
        }

        &.threeItems {
            .item {
                width: 255px;
            }
        }
    }

    .purlModuleBottom {
        float: left;
        padding: 15px 10px;
    }

    .purlCelebrateEmpty {
        color: $brand_color_white;
        text-align: center;
        margin-bottom: $g6ModulePaddingY;
        p {
            margin-top: 40px;
            font-size: 22px;
            font-weight: 600;
            text-align: center;
        }

        a {
            margin-top: 10px;
        }

    }

}/* /.launchModule */

/* ----- PAGE STYLES ----- */
.page.purlCelebrate {
    .spin {
        display: block;
        height: $spinner_widthHeight;
        margin: 0;
        position: absolute;
        right: 50%;
        top: 50%;
        width: $spinner_widthHeight;
    }

    .page-topper {
        color: $purlCelebrate_pageTopperColor;
        background-color: $purlCelebrate_pageTopperBgColor;
        @include safeImgUrls($purlCelebrate_pageTopperBgImg, $purlCelebrate_pageTopperBgImgPath, $property: 'background-image');
        background-size: $purlCelebrate_pageTopperBgImgSize;
        background-position: $purlCelebrate_pageTopperBgImgPosition;

        @if $purlCelebrate_pageTopperBgImgOpacity and $purlCelebrate_pageTopperBgImgOpacity < 1 {
            position: relative;

            &::before {
                display: block;
                content: '';
                position: absolute;
                top: 0;
                right: 0;
                bottom: 0;
                left: 0;
                background: transparentize($purlCelebrate_pageTopperBgColor, $purlCelebrate_pageTopperBgImgOpacity);
            }

            div {
                position: relative;
            }
        }
    }

    #purlPastPresentSelect {
        label {
            display: inline-block;
            width: auto;
            background: #ddd;
            border: none;
            color: $brand_color_black;
            font-size: 14px;
            font-weight: bold;
            text-align: center;
            text-shadow: none;
            @include transition(all 0.1s ease-in-out);
        }

        input {
            display: none;
        }

        input:checked + label {
            background-color: $brand_color_primary;
            color: #fff;
        }

        label:first-of-type {
            border-radius: 4px 0 0 4px;
        }

        label:last-of-type {
            border-radius: 0 4px 4px 0;
        }
    }

    .purlCelebrateView,
    .purlCelebrateFilterWrap {
        display: inline-block;
        margin-right: 30px;

        .toggleLabel,
        .filterLabel {
            display: block;
            margin-bottom: 5px; // borrowed from bootstrap/_forms.scss > label
            font-weight: $fontWeightSemiBold; // copied from _forms.scss > $forms_labelFontWeight;
        }

        .dropdown {
            margin-top: 0;
        }
    }

    .purlCelebrateSearch {
        display: inline-block;

        label {
            display: inline-block;
            // margin: 10px 0;
        }

        .selected-filter {
            display: inline-block;
            list-style: none;
            padding: 5px 8px;
            margin: 5px;
            background: #efefef;
            color: #353a40;
            border-radius: 5px;
            padding: 3px 10px;
            white-space: nowrap;

            .removeSearchToken {
                margin-left: 5px;
                cursor: pointer;
                color: $brand_color_primary;
            }
        }

        .filter-bold {
            font-weight: $fontWeightBold;
        }
    }

    .celebrationTitle {
        display: none;
    }


    .dropdown-menu-info {
        font-size: 80%;
        padding: 5px 8px;
    }

    .purlCelebrateItemsCont {
        .table {
            td {
                vertical-align: middle;
                @media (min-width: $breakpoint_tablet_min){
                    white-space: nowrap;
                }

                a {
                    margin-top: 8px;
                }
                &.pax-name {
                    a {
                        @media (max-width: $breakpoint_mobile_max){
                            display: block;
                        }
                    }
                }
            }

            .avatar {
                background-color: $brand_color_primary;
                border-radius: 50%;
                color: $brand_color_white;
                cursor: pointer;
                display: inline-block;
                font-size: $fontSizeLarge;
                font-weight: normal;
                height: 40px;
                line-height: 40px;
                margin-right: 8px;
                min-width: 40px;
                text-align: center;
                vertical-align: middle;
                width: 40px;
            }
            th.sortable {
                white-space: nowrap;
            }
        }
    }

    .purlCelebrateItems.emptySet {
        h2 {
            font-size: $baseFontSize * 1.429;
        }

        i {
            margin-right: 10px;
        }
    }

    .purlCelebrateSearch {
        .searchWrap {
            margin-top: 0;
            margin-bottom: 0;
            position: relative;

            .btn {
                background: none;
                // border: none;
                // border-radius: 4px;
                // box-shadow: none;
                height: 28px;
                padding: 0 5px;
                // position: absolute;
                right: 2px;
                top: 2px;
                // z-index: 3;
            }
        }

        .searchBtn {
            i {
                color: $purlCelebrate_magnifyColor;
                font-size: 20px;
                line-height: 32px;
                text-shadow: none;
            }
        }

        .spinnerWrap {
            background-color: $brand_color_white;
            border-radius: 4px;
            display: none;
            height: 28px;
            margin: 0;
            padding: 0;
            position: absolute;
            right: 2px;
            top: 2px;
            vertical-align: top;
            width: 28px;
            z-index: 4;
        }
    }
}/* /.purlCelebrate */
